.App {
  text-align: center;
  background-color: #181c20;
  min-height: 100vh;
}

.navbar{
background-color: #181c20;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 50px;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  justify-content: flex-end;
  z-index: 1;
}

.App-header h1 {
  margin: 0;
}

.App-footer {

  height: 50px;
  justify-content: center;
  color: white;
}

.App-footer h1 {
  margin: 0;
}

.App-link {
  color: #61dafb;
}

.Home-section {

  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 100vh;
  margin-left: 10px;
  margin-right: 10px;
  justify-content: center;
}
.section {

  display: flex;
  flex-direction: row;
  min-height: 100vh;
  padding-top: 50px;
 margin-left: 10px;
 margin-right: 10px;
}

.col-l {
  width: 50%;
  height: auto;
  /* padding-top: 50px; */
}
.col-r {
  width: 50%;
  height: auto;
  /* padding-top: 50px; */
}
.p_pic {
  height: 240px;
  width: auto;
  border-radius: 50%;
}
.text-p{
  text-align: left;
  color: white;
}
.text-p h2{
  font-size: 2.5rem;
  margin: 0;
}
.text-p h5{
  font-size: 1.7rem;
  margin: 0;

}
.skills{
  font-family:'Courier New', Courier, monospace;
  color: white;
  margin: 0;
  opacity: 0.5;
}
.socials {
  padding-top: 20px;
  width: 2rem;
  fill: white;
  padding-right: 10px;
  margin-left: auto;
  margin-right: auto;

}

.nav-items{
  text-align: right;
  margin-right: 20px;
  display: flex;
  flex-direction: row;
}
.nav-items h4{
  padding-right: 30px;
  margin: 0;
}

@media screen and (max-width: 500px) {
  .col-l {
    width: 100%;
  }
  .col-r {
    width: 100%;
    /* padding-top: 20px; */
  }
  .text-p{
    padding-left: 20px;
  }
  .Home-section {
    flex-direction: column;
  }
  .section {
    flex-direction: column;
  }
  .p_pic{
    height: 200px;
    padding: 30px;
  }
  .nav-items{
    margin-right: 10px;
  }
  .nav-items h4{
    padding-right: 20px;
  }

.text-p h2{
  font-size: 2.3rem;
  margin: 0;
}
.text-p h5{
  font-size: 1.5rem;
  margin: 0;

}
}
  
a {
  color: inherit;
  text-decoration: inherit;
}
ul {
  margin: 0;
  padding: 10px;
  overflow: hidden;
}
li {
  float: left;
  margin-right: 1rem;
  margin-left: 1rem;
  font-size: 1.2rem;
}
.about {
  font-size: 1.2rem;
}